import React, { useState, useEffect } from 'react';
import './App.css';
import axios from 'axios';
import Spinner from './Spinner';
import Header from './Header';
import JokeDisplay from './JokeDisplay';
import { Container, Jumbotron } from 'react-bootstrap';

const App = () => {
  const [categories, setCategories] = useState([]);
  const [joke, setJoke] = useState('');
  const [loading, setLoading] = useState(false);

  // const fetchList = async () => {

  //   const response = await axios.get(
  //     'https://api.chucknorris.io/jokes/categories'
  //   );
  //   setCategories(response.data);

  // };

  const getCategories = async () => {
    return await axios.get(`https://api.chucknorris.io/jokes/categories`);
  };

  useEffect(() => {
    setLoading(true);
    getCategories().then((c) => {
      setCategories(c.data);
      setLoading(false);
    });
  }, []);

  // useEffect(() => {
  //   setLoading(true);
  //   fetchList(categories);

  // }, [categories]);

  const handleClick = (category) => () => {
    axios
      .get(`https://api.chucknorris.io/jokes/random?category=${category}`)
      .then((data) => setJoke({ ...data }));
  };

  return (
    <>
      <Jumbotron fluid>
        <Container>
          <h1 className='display-5 text-center'>Chuck Norris Jokes</h1>
          <p className='lead text-center'>
            Click on the below categories to get Jokes
          </p>
          {loading ? (
            <Spinner />
          ) : (
            <Header handleClick={handleClick} data={categories} />
          )}
        </Container>
      </Jumbotron>
      {joke ? <JokeDisplay {...joke.data} /> : null}{' '}
    </>
  );
};

export default App;
