import React from 'react';
import {  Nav } from 'react-bootstrap';

const Header = ({ data, handleClick }) => {
    
  const CategoryButton = ({ handleClick, category }) => (
    <Nav.Item>
      <Nav.Link onClick={handleClick(category)}>{category}</Nav.Link>
    </Nav.Item>
  );

  return (
    <Nav>
      {data.map((category) => (
        <CategoryButton
          key={category}
          handleClick={handleClick}
          category={category}
        />
      ))}
    </Nav>
  );
};

export default Header;
